*{
    margin: 0;
    padding: 0;
   

}
header{
   align-items: center;
    width: 100vw;
    min-width: 400px;
    background: #ecf0f1;
    height: 70px;
    display: flex;
    justify-content: space-around;
    z-index: 1;
    box-shadow: 0px 3px 15px 0px #0000004f;
}
header img{
    height: 60px;
    
}
header nav ul{
    display: flex;
    justify-content: center;
    
}

header nav ul li a{
    font-family: arial;
    color: #1b1e20;
    font-weight: 700;
    font-size: 17px;
    margin: 15px;
    text-decoration: none;
    padding-bottom: 5px;
    transition: all .5s ease;
}
header nav ul li a:hover, .active{
    color:#50dad3;
    border-bottom: 2px solid #50dad3;
}

header nav ul li{
    list-style: none;
}
header #menubar{
    display: none;
}
header nav label i{
    font-family: arial;
    display: none;
    padding: 8px;
    color: rgb(0, 36, 59);  
    font-size: 20px;
    font-weight: 800;
    transition: all .5s ease;
    
}
header i:hover{
    color: #50dad3;
    

}


@media (max-width: 900px){
    header nav label i{
        display: block;
              
    }
    header {
        justify-content: space-between;
    }
    header i{
        margin-right: 40px;
    }
    header img{
        margin-left: 40px;
    }
    #menubar:checked ~ ul{
        left: 0;
        transition: all .3s ease-in;
    } 
    #menubar:checked ~ label i::before{
        content: "Close ";
    }
    /* #menubar:checked ~ label i::after{
        content: "Open ";
    } */
  
    header nav ul{
        
        border-bottom-right-radius: 10px;
        position: fixed;
        top: 70px;
        background: #c6d4dbf1;
        
        width: 80vw;
        height: 80vh;
        left: -100%;
        display: block;
        text-align: center;
        
    }
    header nav ul li{
        padding: 3vh;
        border-bottom: 2px solid #cad5db
        
    }
    
    header nav ul li a{
        font-size: 20px;
        font-weight: 550;
        letter-spacing: 1px;
    }
    header nav ul li a:hover, .active{
        color:#50dad3;
        border-bottom: none;
    }
    
}