#footer{
    width: 100%;
    background: rgb(3 24 40);
    /* padding: 30px; */
    font-family: arial;

}
#footer #footer-contents{
    
    padding: 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    

}
#footer #footer-contents .content{ 
    
    width: 30%;
    

}
#footer #footer-contents .content .main-content h3{ 
    padding: 10px;
    font-size: 24px;
    color: rgb(121, 240, 200);
}
#footer #footer-contents .content .main-content{ 
    
    text-align: center;
}
#footer #footer-contents .content .main-content ul li{ 
    
    list-style: none;
    padding: 5px;
    

}
#footer #footer-contents .content .main-content ul li a:hover{
    color: aqua;
 }
#footer #footer-contents .content .main-content ul li a{ 
    transition: all .3s ease;
    text-decoration: none;
    color: aliceblue;
    font-size: 18px;
    

}

.footer-heading h3{
    padding: 35px;
    font-size: 20px;
    letter-spacing: 1px;
    text-align: center;
    color: aliceblue;
}
#footer .copyright p a{
    text-decoration: none;
    font-size: 18px;
    color: rgb(121, 196, 154);
}
#footer .copyright p{
    color: aliceblue;
}
#footer .copyright{
    text-align: center;
    padding: 30px;
}

@media only screen and (max-width: 900px){

    #footer #footer-contents .content .main-content ul li a{ 
       
        
        font-size: 15px;
        
    
    }
    #footer #footer-contents .content .main-content h3{ 
        
        font-size: 18px;
        
    }


}