
*{
    max-width: 100%;
    max-height: 100%;

}
#first{
    overflow-y: hidden;
    width: 100vw;
    min-width: 400px;
    height: 75vh;
    display: flex;
    padding-top:120px;
    background-image: url("back.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    /* background: rgb(243, 243, 243); */

}
#first #first-content{
    align-items: center;
    display: flex;
    flex-direction: column;
    
    font-family: arial;
    text-align: center;
    width: 65%;
    height: auto;
}
#first #first-content h1{
    font-size: 50px;
    /* padding: 30px; */
    margin: -5px;
    letter-spacing: 2px;
}
#first #first-content h1 b{
    font-size: 65px;
    color: orange;
    font-weight: 600;
    letter-spacing: 2px;
}
#first #first-content p{
    font-size: 20px;
    color: rgb(42, 44, 44);
    word-spacing: 3px;
    letter-spacing: 1px;
    
}
#first #first-content .slogan{
    letter-spacing: 7px;
    margin-bottom: 50px;
    color: rgb(255, 255, 255);
    
}


#first #first-content #join-us{
    margin-top: 70px;
    min-height: 40%;
    width: 50%;
    height: auto;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* box-shadow: 0 0 4px #0000008f; */
    /* border: 1px solid #a6a6a6; */
    
}
#first #first-content #join-us h2{
    color: rgb(42, 44, 44);
    border-radius: 5px;
    font-size: 30px;
    letter-spacing: 2px;
    background: #50dad3;
    width: 324px;
    padding: 5px;

}
#first #first-content #join-us #btn button:hover{
    background: orange;
}
#first #first-content #join-us #btn button{
    transition: all .3s ease-in;
    background: #50dad3;
    color: white;
    letter-spacing: 1px;
    font-size: 20px;
    padding: 10px;
    width: 160px;
    font-weight: 500;
    margin: 20px 7px;
    border: none;
    border-radius: 5px;

}
#first #first-content #join-us #btn button:first-child:hover{
    background: #50dad3;
}
#first #first-content #join-us #btn button:first-child{
    
    background: orange;
}

#first #first-content a{
    font-size: 23px;
    font-weight: 600;
    text-decoration: none;
    color: rgb(47, 218, 218);
    
}
#first #first-content h1{
    font-size: 60px;
    padding: 10px;
    letter-spacing: 2px;
}

#first #first-image{ 
    justify-content: center;
    align-items: center;
    
    width: 35%;
    height: auto;
    display: flex;
    flex-direction: column;
    font-family: arial;
}
#first #first-image img{ 
    margin-top: -175px;
    width: 60%;
    height: auto;
    border-radius: 50%;
    border: 5px solid #faa52e;
   
    box-shadow: 0 0 7px #2b2b2b85;
}
#first #first-image a{ 
    margin-top: 30px;
    font-size: 35px;
    letter-spacing: 4px;
    text-decoration: none;
    font-weight: 600;
    color: #faa52e;
}
#first #first-image #expertise{
    width: 50%;
    height: auto;

    display: flex;
    flex-direction: column;
    border: 1px solid #cccfd0;
    box-shadow: 1px 1px 4px 0px #0000008f;
    border-radius: 10px;
    padding: 10px;
    margin: 10px;
}
#first #first-image #expertise span{ 
   
    font-size: 18px;
    text-decoration: none;
    font-weight: 700;
    color: rgb(235, 201, 51);
    margin: 4px;
}
#first #first-image #expertise p{ 
    color: rgb(52, 170, 150);
    font-size: 20px;
    font-weight: 700;
    margin: 4px;

   
}




@media only screen and (max-width: 900px){

#first{
    height: 100%;
    display: flex;
    flex-direction: column;
}
#first #first-content{
    width: 100%;
    height: 100vh;
}
#first #first-image{
    margin-top: 100px;
    width: 100%;
}
#first #first-content .slogan{
    letter-spacing: 5px;
}
#first #first-content p{
    font-size: 17px;
    color: rgb(42, 44, 44);
    word-spacing: 3px;
    font-weight: 500;
    letter-spacing: 1px;
    
}
#first #first-content h1 b{
    font-weight: 900;
}
#first #first-content h1{
    font-size: 40px;
}
#first #first-content #join-us #btn button{
   
    width: 200px;
    font-weight: 700;
    margin: 10px 7px;
   border: 2px solid white;

}
#first #first-content #join-us h2{
   
    font-size: 40px;
    letter-spacing: 5px;
    background: #50dad3;
   margin-bottom: 20px;
    height: auto;
    padding: 5px 60px;

}

}





/* second start from here  */

#second{
    min-width: 400px;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    background: linear-gradient(45deg, #ddc599, #a1cdcb);

}
#second #expertise-info{
   
    width: 40%;
    background-color: #61616149;
    margin-left: 10px;
    border-radius: 5px;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* background: #a1cdcb; */
}
#second #expertise-info img{ 
    width: 200px;
    height: auto;
    
}
#second #expertise-info #expertise-about p span{
    font-size: 40px;
    font-weight: 600;
}
#second #expertise-info #expertise-about p{
    font-family: arial;
    font-size: 18px;
    color: #003544;

 }
 #second #expertise-info #expertise-about p:nth-child(0n+1){
    font-family: arial;
    font-size: 25px;
    font-weight: 700;
    padding: 10px;
    color: orange;
    /* background: #a1cdcb; */
    margin-bottom: 20px;
    text-align: center;
    
 }
#second #expertise-info #expertise-about{ 
    width: 70%;
    
    /* background-color: #61616149; */
    padding: 20px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    text-align:start;
    margin-left: 20px;
    
}
#second #expertise{
    font-family: arial;
    width: 40%;
    padding: 20px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #2196f305;    
    box-shadow: 6px 6px 8px #0000004d;
    border-radius: 5px;

}
#second #expertise p{
    font-size: 25px;
    color: orange;
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin: 10px;

}
#second #expertise .skills{
    /* background: #50dad3; */
    border-radius: 5px;
    width: 100%;
    text-align: left;
}
#second #expertise .skills .bar{
    /* background: #77da50; */
    border-radius: 5px;
    margin: 20px 0;

}
#second #expertise .skills .bar:first-child{
   
    margin-top: 0;

}
#second #expertise .skills .bar .info span{
    /* color: red; */
    font-size: 20px;
    font-weight: 600;
}
#second #expertise .skills .bar .progress-line{
    background: #ffffff;
    border-radius: 5px;
    position: relative;
    /* border-radius: 5px; */
    width: 100%;
    height: 10px;    
    box-shadow: inset 1.5px 1.5px 8px #d1cdc7;


}





#second #expertise .skills .bar .html span{
    width: 95%;
}
#second #expertise .skills .bar .css span{
    width: 95%;
}
#second #expertise .skills .bar .js span{
    width: 70%;
}
#second #expertise .skills .bar .react span{
    width: 85%;
}
#second #expertise .skills .bar .node span{
    width: 85%;
}
#second #expertise .skills .bar .express span{
    width: 80%;
}
#second #expertise .skills .bar .mongodb span{
    width: 75%;
}






#second #expertise .skills .bar .progress-line span{
    background: #00bcd4;
    border-radius: 5px;
    box-shadow: inset 1.5px 1.5px 8px #1d99ba;
    height: 100%;
    position: absolute;


}

