#main{
    background: #eeeeee;
    font-family: Arial, Helvetica, sans-serif;
    
}
#main #list-of-courses{
    padding-top: 20px;

    display: flex;
    flex-direction: column;
    align-items: center;
}
.heading h1{
    margin: 50px 0;
    font-size: 35px;
    font-weight: 600;
    letter-spacing: 1px;
    color: rgb(49, 48, 48);
}
.heading h1 span{
    font-size: 40px;
    font-weight: 900;
    color: orange;


}
#main .courses{ 
    min-width: 400px;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    
}
#main .courses .course-one h2{
    font-size: 35px;
    font-weight: 800;
    letter-spacing: 1px;
    margin: 10px;
    color: rgb(255, 255, 255);


} 
 
#main .courses .course-one p{
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 1px;
    margin: 10px;
    color: rgb(218, 240, 255);


}
#main .courses .course-one:first-child{
    background-image:linear-gradient(rgb(255 152 0 / 74%), rgba(0, 0, 0, 0.911)), url(html5.jpeg);
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    
}

#main .courses .course-one:hover{ 
    box-shadow: 0px 0px 14px 3px #000000b5;
}
#main .courses .course-one{
    transition: all .5s ease;
    width: 25%;
    margin: 20px;
    padding: 20px;
    height: auto;
    background-image:linear-gradient(rgb(3 169 244 / 64%), rgba(0, 0, 0, 0.911)), url(css.jpeg);
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    border-radius: 5px;

}
#main .courses .course-one:last-child{
    background-image:linear-gradient(rgb(76 175 80 / 78%), rgba(0, 0, 0, 0.911)), url(js.png);
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    
}
#main .courses .course-one div{
    text-align: center;
    
}
#main .courses .course-one div button:hover{
    cursor: pointer;
    color: #ffffff;
    border: 2px solid rgb(255, 255, 255);
    background: linear-gradient(338deg,#a4e2ff, #ff9900);
 }
#main .courses .course-one div button{ 
    border: 2px solid rgb(155, 172, 184);
    transition: 0.5s;
    font-size: 20px;
    font-weight: 800;
    margin: 10px;
    width: 150px;
    height: auto;
    padding: 5px;
    background: linear-gradient(338deg,#ffc163, #afe6ff);
    
    border-radius: 20px;
    

}




@media only screen and (max-width: 900px) {
    /* For mobile phones: */
    #main .courses{ 
       width: 100vw;
        flex-direction: column;
       align-items: center;
     
        
    }
    #main .courses .course-one{
        width: 90vw;
        padding: 5px;
        margin: 20px 0;
    }
    #main .courses .course-one p{
        font-size: 17px;
        
    
    
    }
    .heading h1{
        text-align: center;
        margin: 50px 0;
        font-size: 30px;
        font-weight: 600;
        letter-spacing: 1px;
        color: rgb(30, 103, 212);
    }
    .heading h1 span{
        font-size: 40px;
        font-weight: 900;
        color: orange;
    
    
    }






  }